header#home-header,
header#product-header {
  display: flex;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  margin: 0 auto;
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center center;
  background-attachment: fixed;
  position: relative;
  overflow: hidden;
}

header#home-header::after,
header#product-header::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(48, 13, 1, 0.6862745098);
}

header#home-header .header_content,
header#product-header .header_content {
  display: flex;
  flex-flow: column;
  align-content: center;
  align-items: center;
  position: relative;
  color: #fff;
  z-index: 55;
  text-align: center;
  width: 100%;
  padding: 0 20px;
}

header#home-header .header_content h1,
header#product-header .header_content h1 {
  text-transform: uppercase;
  font-size: 60px;
  letter-spacing: 2px;
}

header#home-header .header_content p,
header#product-header .header_content p {
  line-height: 30px;
  margin-bottom: 20px;
  max-width: 600px;
  font-size: 18px;
}

header#home-header .header_content .header_content__buttons,
header#product-header .header_content .header_content__buttons {
  display: flex;
}

header#home-header .header_content .header_content__buttons a,
header#home-header .header_content .header_content__buttons button,
header#product-header .header_content .header_content__buttons a,
header#product-header .header_content .header_content__buttons button {
  display: block;
  padding: 15px 40px;
  border-radius: 16px;
  border: none;
  cursor: pointer;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
  position: relative;
  overflow: hidden;
}

header#home-header .header_content .header_content__buttons a,
header#product-header .header_content .header_content__buttons a {
  color: #fff;
  background: #e3774c;
  text-decoration: none;
  margin-right: 20px;
  transition: background 0.3s;
}

header#home-header .header_content .header_content__buttons a:hover,
header#product-header .header_content .header_content__buttons a:hover {
  background: #f55d22;
}

header#home-header .header_content .header_content__buttons button,
header#product-header .header_content .header_content__buttons button {
  color: #fff;
  border: 2px solid #fff;
  background: transparent;
  font-size: 16px;
}

header#home-header .header_content .header_content__buttons button::before,
header#product-header .header_content .header_content__buttons button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background: rgba(255, 255, 255, 0.288);
  transition: width 0.15s;
  z-index: -1;
}

header#home-header .header_content .header_content__buttons button:hover::before,
header#product-header .header_content .header_content__buttons button:hover::before {
  width: 100%;
  z-index: -1;
}

header#home-header .scrollDown,
header#product-header .scrollDown {
  width: 100%;
  position: relative;
  text-align: center;
  z-index: 55;
  cursor: pointer;
  top: 110px;
  animation: moveing 0.7s linear alternate infinite;
}

header#home-header svg,
header#product-header svg {
  width: 70px;
  height: 70px;
  fill: #fff;
}

@keyframes moveing {
  from {
    top: 110px;
  }

  to {
    top: 140px;
  }
}

header#home-header {
  height: 90vh;
}

header#product-header {
  height: 200px;
  max-height: 200px;
  min-height: 200px;
}

header#product-header .header_content h1 {
  font-size: 30px;
}

main h2 {
  text-align: center;
  margin-bottom: 30px;
}

main .items-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  grid-gap: 20px;
  margin-bottom: 60px;
}

main .items-wrapper .item {
  max-width: 100%;
}

main .our_team .our_team__member {
  text-align: center;
}

main .our_team .our_team__member .otm_img {
  overflow: hidden;
  border-radius: 15px;
  margin-bottom: 15px;
}

main .our_team .our_team__member .otm_img img {
  display: block;
  width: 100%;
  height: 320px;
  object-fit: cover;
  object-position: center center;
}

main .our_team .our_team__member h3 {
  color: #e3774c;
  margin-bottom: 8px;
}

main .our_team .our_team__member p {
  font-size: 13px;
  font-style: italic;
}

main .product-page .product {
  margin-top: 50px;
  display: flex;
  align-items: flex-start;
  gap: 100px;
}

main .product-page .product .product-imgs {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-basis: 60%;
}

main .product-page .product .product-imgs a {
  display: block;
  width: 25%;
  max-height: 200px;
  min-height: 200px;
  overflow: hidden;
}

main .product-page .product .product-imgs a img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
  opacity: 0.3;
  transition: opacity 0.3s;
}

main .product-page .product .product-imgs a:nth-child(1) {
  flex-basis: 100%;
  min-height: 400px;
}

main .product-page .product .product-imgs a:nth-child(1) img {
  opacity: 1;
}

main .product-page .product .product-imgs a:hover img {
  opacity: 1;
}

main .product-page .product .product_options_and_basket {
  flex-basis: 40%;
}

main .product-page .product .product_options_and_basket .product_options .product_option {
  display: flex;
  justify-content: space-between;
  padding-bottom: 10px;
  border-bottom: 2px solid #ddd6d6;
  margin-bottom: 10px;
}

main .product-page .product .product_options_and_basket .product_options .product_option .product_option__title {
  font-weight: bold;
}

main .product-page .product .product_options_and_basket .product_options .product_option .product_option__value {
  font-style: italic;
}

main .product-page .product .product_options_and_basket #about_product {
  color: #5d5d5d;
  margin: 15px 0px;
  font-size: 15px;
  letter-spacing: 1px;
  font-style: italic;
}

main .product-page .product .product_options_and_basket #product_price {
  font-weight: bold;
  margin: 10px 0 35px 0;
  letter-spacing: 1px;
  color: #1a1a1a;
}

main .product-page .product .product_options_and_basket .basket_block {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

main .product-page .product .product_options_and_basket .basket_block a {
  display: block;
  border-radius: 16px;
  text-decoration: none;
}

main .product-page .product .product_options_and_basket .basket_block #buy {
  background: #e3774c;
  color: #fff;
  padding: 10px 45px;
  transition: 0.3s;
}

main .product-page .product .product_options_and_basket .basket_block #buy:hover {
  background: #f55d22;
}

main .product-page .product .product_options_and_basket .basket_block #credit {
  color: rgb(0, 153, 64);
  font-weight: bold;
}

