@font-face {
  font-family: NSABold;
  src: url("../fonts/NotoSansArmenian-Bold.ttf");
}

@font-face {
  font-family: NSABold;
  src: url("../fonts/NotoSansArmenian-Bold.ttf");
}

@font-face {
  font-family: NSARegular;
  src: url("../fonts/NotoSansArmenian-Regular.ttf");
}

@font-face {
  font-family: NSAMedium;
  src: url("../fonts/NotoSansArmenian-Medium.ttf");
}

@font-face {
  font-family: NSALight;
  src: url("../fonts/NotoSansArmenian-Light.ttf");
}

@font-face {
  font-family: NSAThin;
  src: url("../fonts/NotoSansArmenian-Thin.ttf");
}

body {
  font-family: NSARegular, "Roboto" sans-serif;
  overflow-x: hidden;
}

body::-webkit-scrollbar {
  width: 5px;
  height: 7px;
  overflow: hidden;
}

body::-webkit-scrollbar-track {
  background: #ccc;
}

body::-webkit-scrollbar-thumb {
  background: #e3774c;
}

p {
  line-height: 22px;
}

a {
  color: #4088fc;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
  color: #e3774c;
}

.swiper-button-next,
.swiper-button-prev {
  top: 45% !important;
}

.swiper-button-next {
  background-image: url(../img/right-arrow.png) !important;
}

.swiper-button-prev {
  background-image: url(../img/left-arrow.png) !important;
}

@media screen and (max-width: 1680px) {
  header#home-header,
  header#product-header {
    background-size: cover;
  }
}

@media screen and (max-width: 1280px) {
  .title {
    font-size: 40px;
  }

  .title {
    font-size: 40px;
  }

  .title {
    font-size: 40px;
  }

  .title {
    font-size: 40px;
  }
}

@media screen and (max-width: 1024px) {
  .title {
    font-size: 35px;
  }

  .title {
    font-size: 35px;
  }

  .title {
    font-size: 35px;
  }

  footer .container {
    flex-wrap: wrap;
  }

  footer .container .footer-block {
    flex-basis: 48%;
  }

  .title {
    font-size: 35px;
  }
}

@media screen and (max-width: 768px) {
  .title {
    font-size: 30px;
  }

  nav .container .nav_menu .menu_btn {
    display: flex;
  }

  nav .container .nav_menu ul {
    position: absolute;
    flex-flow: column;
    justify-content: flex-start;
    top: -10px;
    width: 580px;
    overflow: hidden;
    background: #e2e2fa;
    transition: left 0.2s linear;
    left: -5000px;
    padding: 0 25px;
  }

  nav .container .nav_menu ul li a {
    left: 0;
    width: 100%;
    height: 60px;
    padding: 30px 20px;
  }

  nav .container .nav_menu ul.active_nav {
    height: 100vh;
    left: 0;
  }

  .title {
    font-size: 30px;
  }

  header#home-header,
  header#product-header {
    height: 600px;
  }

  header#home-header .scrollDown,
  header#product-header .scrollDown {
    top: 60px !important;
    animation-play-state: paused;
  }

  header#home-header .header_content h1,
  header#product-header .header_content h1 {
    font-size: 45px;
  }

  header#home-header .header_content p,
  header#product-header .header_content p {
    font-size: 15px;
  }

  main .product-page .product {
    flex-wrap: wrap;
  }

  main .product-page .product .product-imgs,
  main .product-page .product .product_options_and_basket {
    flex-basis: 100%;
  }

  .title {
    font-size: 30px;
  }

  footer .container {
    flex-wrap: wrap;
  }

  footer .container .footer-block {
    flex-basis: 100%;
    text-align: center;
  }

  .title {
    font-size: 30px;
  }
}

@media screen and (max-width: 768px) and (max-width: 680px) {
  nav .container .nav_menu ul {
    width: 500px;
  }
}

@media screen and (max-width: 768px) and (max-width: 580px) {
  nav .container .nav_menu ul {
    width: 450px;
  }
}

@media screen and (max-width: 768px) and (max-width: 520px) {
  nav .container .nav_menu ul {
    width: 400px;
  }
}

@media screen and (max-width: 768px) and (max-width: 480px) {
  nav .container .nav_menu ul {
